import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

/**
 * The interface of a tab to be displayed on the journey layout.
 */
export interface Tab {
  /**
   * Title of the tab which will be displayed on the tab navigation component.
   */
  title: string;
  /**
   * Route of the tab.
   */
  route: string;
}

@Component({
  selector: 'bb-messages-tabs',
  templateUrl: './messages-tabs.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MessagesTabsComponent {
  /**
   * Tabs to render
   */
  @Input() tabs!: Tab[];
}
